<script>

import StaticGrid from './components/StaticGrid.vue';
import Loader from './components/Loader.vue';
import Logo from './components/Logo.vue';

export default {
  name: 'App',
  data() {
    return {
      initialLoad: true,
      loader: false,
      viewChange: false,
      newDelay: false,
      currentView: this.$router.currentRoute.fullPath,
      logo: {
        width: "140px",
        color: "#fff"
      }
    }
  },
  mounted(){
    this.initialLoad = false;
    if(window.innerWidth < 768) {
      this.logo.color = "#000";
    }
  },
  methods: {
    changeLogoColor(color){
      this.logo.color = color;
    },
    goTo(path) {
      if(path === this.currentView) return;
      this.loader = true;
      this.viewChange = true;
      this.newDelay = true;
      setTimeout(()=> {
        this.loader = false;
      }, 1500);

      setTimeout(()=>{
        this.currentView = path;
        this.$router.push(path);
        this.viewChange = false;
      }, 1700);
    }
  },
  components: {
    StaticGrid,
    Loader,
    Logo
  }
}
</script>
<template>
  <div id="app">
    <StaticGrid/>
    <transition name="loader">
      <Loader v-if="loader || initialLoad" v-bind:initialLoader="initialLoad"/>
    </transition>
    <section class="app-wrap">
      <header class="navigation" v-bind:class="{ 'leaving-nav': viewChange }">
        <div class="container">
          <div class="row align-items-center">
            <div class="col">
              <a href="#" @click.prevent="goTo('/')" class="logo">
                <!-- <img src="./assets/er_logo.svg" width="140" alt=""> -->
                <Logo v-bind:width="logo.width" v-bind:color="logo.color"/>
              </a>
            </div>
            <div class="col text-right">
              <a href="#" @click.prevent="goTo('/contact')" class="contacts letter-animation">
                <span>К</span>
                <span>О</span>
                <span>Н</span>
                <span>Т</span>
                <span>А</span>
                <span>К</span>
                <span>Т</span>
                <span>Ы</span>
              </a>
            </div>
          </div>
        </div>
      </header>
      <div class="view" v-bind:class="{ 'leaving': viewChange, 'changeDelay': newDelay }">
        <router-view/>
      </div>
    </section>
  </div>
</template>


<style lang="scss">
@import './assets/css/_grid.min.scss';
@import './assets/css/_global.scss';
@import './assets/css/_app.scss';
