<template>
    <div class="image__reveal" v-bind:class=" { 'out': !visible } " v-waypoint="{ active: true, callback: onWaypoint, options: intersectionOptions }">
        <div class="reveal__outer-wrapper">
            <div class="reveal__inner-wrapper">
                
<slick
  ref="slick"
  :options="slickOptions"
  >
  <img width="100%" src="../assets/slide1.jpg">
  <img width="100%" src="../assets/slide2.jpg">
  <img width="100%" src="../assets/slide3.jpg">
</slick>
            </div>
        </div>
        <div class="slick--buttons">
            <SlickButtons/>
        </div>
    </div>
</template>

<style lang="scss">
.changeDelay {
  .reveal {
      &__outer-wrapper, &__inner-wrapper {
          transition-delay: 2s !important;
      }
  }
}

.slick--buttons {
    position: absolute;
    bottom: 70px !important;
    right: -200px;
    z-index: 200;
    @media(max-width: 767px){
        right: calc(50% - 50px) !important; 
        bottom: -100px;
    }
}
</style>


<script>
import Slick from 'vue-slick';
import SlickButtons from './SlickButtons.vue';
export default {
    name: 'SlickCarousel',
    data() {
        return {
            visible: false,
            slickOptions: {
                slidesToShow: 1,
                slidesToScroll: 1
            }
        }
    },
    components: { Slick, SlickButtons },  
    methods: {
    next() {
        this.$refs.slick.next();
    },
    prev() {
        this.$refs.slick.prev();
    },
    onWaypoint ({ going, direction }) {
      if (going === this.$waypointMap.GOING_IN) {
          this.visible = true
      }
    }
  }
}
</script>
