<script>
import Cube from '../components/Cube.vue';
import ImageR from '../components/ImageRevealed.vue';
import Form from '../components/Form.vue';

export default {
  name: 'Contact',
  components: {
    Cube,
    ImageR,
    Form
  },
  data(){
    return {
      showCube: false,
      intersectionOptions: {
        root: null,
        rootMargin: '100px',
        threshold: 1
      }
    }
  },
  props: {
    name: String
  },
  methods: {
    contactForm ({ going, direction }) {
      if(window.innerWidth > 767) {
        if (going === this.$waypointMap.GOING_IN && direction === this.$waypointMap.DIRECTION_TOP) {
          this.$parent.changeLogoColor('#000');
        }
      }
    },
    contactFirst ({ going, direction }) {
      if(window.innerWidth > 767) {
        if (going === this.$waypointMap.GOING_IN && direction === this.$waypointMap.DIRECTION_BOTTOM) {
          this.$parent.changeLogoColor('#fff');
        }
      }
    }
  },
  mounted(){
    this.showCube = true;
    
  }
}
</script>
